
div {
  display: inline-block;
}
button {
  width: 100px;
  height: 100px;
  margin: 10px;
  border: none;
  border-radius: 50px;
  text-align: center;
  vertical-align: middle;
  color: black;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.4);
          box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
button:hover {
    opacity: 0.7;
}
button a {
    color: black;
}
button a:hover {
      color: black;
}
button:active {
    -webkit-box-shadow: none;
            box-shadow: none;
}



