
.activities .activity {
  position: relative;
  width: 780px;
  background-color: #fff;
  border: 1px solid #ccc;
  margin: 10px 0;
  text-align: left;
  list-style-type: none;
  font-size: 1.4rem;
}
.activities .activity .datetime {
    display: block;
    padding: 5px;
    font-size: 1.2rem;
}
.activities .activity:hover {
    background-color: #f4f4f4;
}
.activities .activity a {
    padding: 20px 10px;
    color: #000;
    display: block;
    width: 100%;
    text-decoration: none;
}
.activities .activity .title {
    margin-top: 10px;
    padding: 10px;
}
.activities .activity .fa-icon {
    position: absolute;
    vertical-align: middle;
    color: #ccc;
    right: 20px;
    bottom: 0;
}



